<template>
    <div style="width: 100vw; height: 100vh; overflow: scroll;">
        <button @click="kakao_login">
            로그인
        </button>
        <div>
            <input type="text" v-model="message_kakao" />
            <button @click="submit_kakao()">전송</button>
        </div>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    data() {
        return {
            message_kakao: "",
            access_token: ""
        }
    },
    methods: {
        async kakao_login() {
            window.location.href = 'https://kauth.kakao.com/oauth/authorize?client_id=642c9af9eefb5b409aa0880459c1da1f&redirect_uri=http://localhost:8080/auth&response_type=code'
        },
        async submit_kakao() {
            let temp = '{"description": "' + this.message_kakao + '"}';
            console.log(temp);
            await axios.post("https://kapi.kakao.com/v2/api/talk/memo/send", {
                template_id: "98470",
                // template_args: '{"description": "sefsefsef32323sef"}'
                template_args: temp
            }, { headers: { Authorization: "Bearer " + this.access_token, "Content-Type": "application/x-www-form-urlencoded" } });
        }
    }
}
</script>